@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&family=JetBrains+Mono:wght@300;400;500;600;700&display=swap');
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

/* Enhanced Paper-Style Variables */
:root {
  /* Core Colors */
  --bg-primary: #fdfcf9;
  --bg-secondary: #f8f7f4;
  --bg-tertiary: #f3f2ef;
  --bg-quaternary: #eeebe6;
  
  /* Text Colors */
  --text-primary: #2c2c2c;
  --text-secondary: #5a5a5a;
  --text-tertiary: #8a8a8a;
  --text-quaternary: #b5b5b5;
  
  /* Border Colors */
  --border-primary: #e5e4e1;
  --border-secondary: #d1d0cd;
  --border-accent: #c7c6c3;
  
  /* Accent Colors */
  --accent-blue: #3b82f6;
  --accent-purple: #8b5cf6;
  --accent-emerald: #10b981;
  --accent-orange: #f59e0b;
  
  /* Glass Effect */
  --glass-bg: rgba(255, 255, 255, 0.7);
  --glass-border: rgba(255, 255, 255, 0.2);
  
  /* Shadows */
  --shadow-light: 0 2px 8px rgba(0, 0, 0, 0.05);
  --shadow-medium: 0 4px 16px rgba(0, 0, 0, 0.08);
  --shadow-heavy: 0 8px 32px rgba(0, 0, 0, 0.12);
  
  /* Paper Effects */
  --paper-shadow-soft: 0 2px 8px rgba(139, 115, 85, 0.08), 0 1px 3px rgba(139, 115, 85, 0.06);
  --paper-shadow-medium: 0 4px 16px rgba(139, 115, 85, 0.12), 0 2px 6px rgba(139, 115, 85, 0.08);
  --paper-shadow-strong: 0 8px 32px rgba(139, 115, 85, 0.16), 0 4px 12px rgba(139, 115, 85, 0.12);
  --paper-shadow-inset: inset 0 1px 2px rgba(139, 115, 85, 0.06);
  --paper-shadow-lifted: 0 12px 40px rgba(139, 115, 85, 0.2), 0 6px 20px rgba(139, 115, 85, 0.15);
  
  /* Paper Texture */
  --paper-texture: 
    radial-gradient(circle at 25% 25%, rgba(0, 0, 0, 0.01) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(0, 0, 0, 0.005) 0%, transparent 50%),
    linear-gradient(90deg, transparent 48%, rgba(0, 0, 0, 0.003) 49%, rgba(0, 0, 0, 0.003) 51%, transparent 52%);
  
  /* Typography */
  --code-font: 'Fira Code', 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  
  /* Terminal Colors - Light Theme */
  --terminal-prompt: #059669;
  --terminal-user: #1d4ed8;
  --terminal-path: #7c2d12;
  --terminal-command: #b45309;
  --terminal-output: #374151;
  --terminal-string: #dc2626;
  --terminal-comment: #6b7280;
  --terminal-cursor: #059669;
  --terminal-highlight: #1d4ed8;
  
  /* Animation performance */
  --hardware-acceleration: translateZ(0);
}

.dark {
  /* Core Colors */
  --bg-primary: #0f0f0f;
  --bg-secondary: #1a1a1a;
  --bg-tertiary: #262626;
  --bg-quaternary: #333333;
  
  /* Text Colors */
  --text-primary: #f5f5f5;
  --text-secondary: #d4d4d4;
  --text-tertiary: #a3a3a3;
  --text-quaternary: #737373;
  
  /* Border Colors */
  --border-primary: #2a2a2a;
  --border-secondary: #404040;
  --border-accent: #525252;
  
  /* Glass Effect */
  --glass-bg: rgba(0, 0, 0, 0.7);
  --glass-border: rgba(255, 255, 255, 0.1);
  
  /* Shadows */
  --shadow-light: 0 2px 8px rgba(0, 0, 0, 0.3);
  --shadow-medium: 0 4px 16px rgba(0, 0, 0, 0.4);
  --shadow-heavy: 0 8px 32px rgba(0, 0, 0, 0.5);
  
  /* Paper Effects */
  --paper-shadow-soft: 0 2px 8px rgba(0, 0, 0, 0.25), 0 1px 3px rgba(0, 0, 0, 0.15);
  --paper-shadow-medium: 0 4px 16px rgba(0, 0, 0, 0.35), 0 2px 6px rgba(0, 0, 0, 0.2);
  --paper-shadow-strong: 0 8px 32px rgba(0, 0, 0, 0.45), 0 4px 12px rgba(0, 0, 0, 0.25);
  --paper-shadow-inset: inset 0 1px 2px rgba(0, 0, 0, 0.2);
  --paper-shadow-lifted: 0 12px 40px rgba(0, 0, 0, 0.6), 0 6px 20px rgba(0, 0, 0, 0.3);
  
  /* Paper Texture */
  --paper-texture: 
    radial-gradient(circle at 25% 25%, rgba(255, 255, 255, 0.01) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(255, 255, 255, 0.005) 0%, transparent 50%),
    linear-gradient(90deg, transparent 48%, rgba(255, 255, 255, 0.003) 49%, rgba(255, 255, 255, 0.003) 51%, transparent 52%);

  /* Terminal Colors - Dark Theme */
  --terminal-prompt: #34d399;
  --terminal-user: #60a5fa;
  --terminal-path: #a78bfa;
  --terminal-command: #fbbf24;
  --terminal-output: #d1d5db;
  --terminal-string: #f87171;
  --terminal-comment: #6b7280;
  --terminal-cursor: #34d399;
  --terminal-highlight: #60a5fa;
}

/* Base Styles */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  line-height: 1.6;
  scroll-padding-top: 100px;
}

body {
  margin: 0;
  font-family: var(--font-primary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  transition: background-color 0.3s ease, color 0.3s ease;
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
  /* Hardware acceleration for smoother animations */
  will-change: background-color, color;
  /* Add padding to prevent content from going under fixed header */
  padding-top: 80px;
}

/* Enhanced Paper Texture Background */
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    /* Paper fiber texture */
    radial-gradient(circle at 25% 25%, rgba(139, 115, 85, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(139, 115, 85, 0.025) 0%, transparent 50%),
    radial-gradient(circle at 50% 10%, rgba(139, 115, 85, 0.02) 0%, transparent 40%),
    radial-gradient(circle at 90% 80%, rgba(139, 115, 85, 0.015) 0%, transparent 30%),
    /* Subtle grid pattern */
    linear-gradient(45deg, transparent 48%, rgba(139, 115, 85, 0.008) 49%, rgba(139, 115, 85, 0.008) 51%, transparent 52%),
    linear-gradient(-45deg, transparent 48%, rgba(139, 115, 85, 0.006) 49%, rgba(139, 115, 85, 0.006) 51%, transparent 52%);
  background-size: 400px 400px, 300px 300px, 200px 200px, 150px 150px, 40px 40px, 60px 60px;
  pointer-events: none;
  z-index: -1;
  background: var(--paper-texture);
  background-size: 100px 100px, 80px 80px, 20px 20px;
  background-position: 0 0, 40px 40px, 0 0;
  opacity: 0.8;
  transition: opacity 0.4s ease;
}

.dark body::before {
  background-image: 
    radial-gradient(circle at 25% 25%, rgba(255, 255, 255, 0.02) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(255, 255, 255, 0.015) 0%, transparent 50%),
    radial-gradient(circle at 50% 10%, rgba(255, 255, 255, 0.01) 0%, transparent 40%),
    radial-gradient(circle at 90% 80%, rgba(255, 255, 255, 0.008) 0%, transparent 30%),
    linear-gradient(45deg, transparent 48%, rgba(255, 255, 255, 0.004) 49%, rgba(255, 255, 255, 0.004) 51%, transparent 52%),
    linear-gradient(-45deg, transparent 48%, rgba(255, 255, 255, 0.003) 49%, rgba(255, 255, 255, 0.003) 51%, transparent 52%);
  opacity: 0.3;
  background: var(--paper-texture);
  background-size: 120px 120px, 90px 90px, 25px 25px;
  background-position: 10px 10px, 50px 50px, 5px 5px;
}

code {
  font-family: 'JetBrains Mono', 'Fira Code', Consolas, 'Courier New', monospace;
  background: var(--bg-tertiary);
  padding: 2px 6px;
  border-radius: 6px;
  font-size: 0.9em;
  border: 1px solid var(--border-primary);
}

/* Enhanced Scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-secondary);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: var(--paper-brown);
  border-radius: 4px;
  transition: all 0.3s ease;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-blue);
}

/* Smooth scrolling for anchor links */
html {
  scroll-behavior: smooth;
  scroll-padding-top: 100px; /* Account for fixed header when scrolling to anchors */
}

/* Backdrop filter support check */
@supports (backdrop-filter: blur(20px)) {
  .header-glass {
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
  }
}

@supports not (backdrop-filter: blur(20px)) {
  .header-glass {
    background-color: rgba(255, 255, 255, 0.95);
  }
  
  .dark .header-glass {
    background-color: rgba(15, 23, 42, 0.95);
  }
}

/* Selection */
::selection {
  background-color: var(--accent-blue);
  color: white;
}

/* Focus States */
button:focus-visible,
a:focus-visible,
input:focus-visible,
textarea:focus-visible {
  outline: 2px solid var(--accent-blue);
  outline-offset: 2px;
  border-radius: 4px;
}

/* Typography with anti-blur */
h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: -0.01em;
  /* Prevent blur in heading animations */
  transform: translateZ(0);
  will-change: transform;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

/* Gradient text with anti-blur */
.text-gradient {
  background: linear-gradient(
    135deg,
    var(--accent-blue) 0%,
    var(--accent-purple) 50%,
    var(--accent-emerald) 100%
  );
  background-size: 200% 200%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradient-shift 6s ease-in-out infinite;
  /* Prevent blur in text gradient animations */
  transform: translateZ(0);
  will-change: background-position;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

/* Enhanced Paper Card */
.paper-card {
  background: var(--bg-secondary);
  border: 1px solid var(--border-primary);
  border-radius: 16px;
  position: relative;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(12px);
  box-shadow: var(--shadow-light);
  /* Hardware acceleration for smooth animations */
  transform: translateZ(0);
  will-change: transform, box-shadow;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

/* Enhanced Paper Texture Effect */
.paper-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    /* Main paper texture */
    radial-gradient(circle at 20% 80%, rgba(139, 115, 85, 0.04) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(139, 115, 85, 0.035) 0%, transparent 50%),
    radial-gradient(circle at 60% 60%, rgba(139, 115, 85, 0.02) 0%, transparent 40%),
    /* Fine grain texture */
    linear-gradient(135deg, transparent 49%, rgba(139, 115, 85, 0.012) 50%, transparent 51%),
    linear-gradient(45deg, transparent 49%, rgba(139, 115, 85, 0.008) 50%, transparent 51%),
    /* Subtle watermark effect */
    radial-gradient(ellipse at center, transparent 60%, rgba(139, 115, 85, 0.005) 70%, transparent 80%);
  background-size: 200px 200px, 150px 150px, 100px 100px, 30px 30px, 20px 20px, 400px 400px;
  pointer-events: none;
  border-radius: inherit;
  opacity: 0.7;
  transition: opacity 0.3s ease;
}

.dark .paper-card::before {
  background-image: 
    radial-gradient(circle at 20% 80%, rgba(255, 255, 255, 0.025) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.02) 0%, transparent 50%),
    radial-gradient(circle at 60% 60%, rgba(255, 255, 255, 0.015) 0%, transparent 40%),
    linear-gradient(135deg, transparent 49%, rgba(255, 255, 255, 0.008) 50%, transparent 51%),
    linear-gradient(45deg, transparent 49%, rgba(255, 255, 255, 0.005) 50%, transparent 51%),
    radial-gradient(ellipse at center, transparent 60%, rgba(255, 255, 255, 0.003) 70%, transparent 80%);
}

/* Paper fold effect for cards */
.paper-card::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  background: 
    linear-gradient(-45deg, transparent 45%, var(--border-primary) 50%, var(--bg-tertiary) 55%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

/* Hover Effects */
.paper-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--paper-shadow-medium);
  border-color: var(--border-accent);
}

.paper-card:hover::before {
  opacity: 0.9;
}

.paper-card:hover::after {
  opacity: 0.6;
}

/* Enhanced Glass Card */
.glass-card {
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: 16px;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  box-shadow: var(--paper-shadow-soft);
  position: relative;
  transition: all 0.3s ease;
}

.dark .glass-card {
  background: rgba(33, 28, 22, 0.8);
  border-color: rgba(107, 93, 74, 0.25);
}

.glass-card:hover {
  background: rgba(250, 249, 247, 0.95);
  border-color: var(--accent-blue);
  box-shadow: var(--paper-shadow-medium);
}

.dark .glass-card:hover {
  background: rgba(33, 28, 22, 0.95);
}

/* Enhanced Button Styles */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  text-decoration: none;
  border: 2px solid transparent;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  cursor: pointer;
  font-size: 0.875rem;
  line-height: 1.25rem;
  white-space: nowrap;
  /* Prevent blur in button animations */
  transform: translateZ(0);
  will-change: transform, background-color, border-color;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* Enhanced Button Shine Effect */
.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.6s;
}

.btn:hover:not(:disabled)::before {
  left: 100%;
}

.btn-primary {
  background: linear-gradient(135deg, var(--accent-blue), var(--accent-purple));
  color: white;
  box-shadow: 0 4px 14px 0 rgba(37, 99, 235, 0.25);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px) scale(1.02);
  box-shadow: 0 8px 25px 0 rgba(37, 99, 235, 0.35);
}

.btn-secondary {
  background: var(--bg-secondary);
  color: var(--text-primary);
  border: 2px solid var(--border-primary);
  box-shadow: var(--paper-shadow-soft);
}

.btn-secondary:hover:not(:disabled) {
  transform: translateY(-2px) scale(1.02);
  border-color: var(--accent-blue);
  color: var(--accent-blue);
  box-shadow: var(--paper-shadow-medium);
}

.btn-ghost {
  background: transparent;
  color: var(--text-secondary);
  border: 1px solid var(--border-secondary);
}

.btn-ghost:hover:not(:disabled) {
  background: var(--bg-tertiary);
  color: var(--accent-blue);
  border-color: var(--accent-blue);
}

/* Enhanced Grid Background */
.grid-bg {
  background-image: 
    linear-gradient(rgba(139, 115, 85, 0.1) 1px, transparent 1px),
    linear-gradient(90deg, rgba(139, 115, 85, 0.1) 1px, transparent 1px);
  background-size: 40px 40px;
  opacity: 0.3;
}

.dark .grid-bg {
  background-image: 
    linear-gradient(rgba(255, 255, 255, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255, 255, 255, 0.05) 1px, transparent 1px);
}

/* Enhanced Terminal */
.terminal {
  background: linear-gradient(135deg, 
    rgba(248, 250, 252, 0.95) 0%, 
    rgba(241, 245, 249, 0.98) 50%, 
    rgba(248, 250, 252, 0.95) 100%);
  border: 1px solid rgba(203, 213, 225, 0.5);
  border-radius: 12px;
  overflow: hidden;
  position: relative;
  backdrop-filter: blur(12px);
  box-shadow: 
    0 25px 50px -12px rgba(0, 0, 0, 0.1),
    0 0 0 1px rgba(255, 255, 255, 0.8) inset;
  transition: all 0.3s ease;
}

.dark .terminal {
  background: linear-gradient(135deg, 
    rgba(15, 23, 42, 0.98) 0%, 
    rgba(30, 41, 59, 1) 50%, 
    rgba(15, 23, 42, 0.98) 100%);
  border-color: rgba(71, 85, 105, 0.4);
  box-shadow: 
    0 25px 50px -12px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(255, 255, 255, 0.05) inset;
}

.terminal:hover {
  transform: translateY(-2px);
  box-shadow: 
    0 32px 64px -12px rgba(0, 0, 0, 0.15),
    0 0 0 1px rgba(255, 255, 255, 0.9) inset;
}

.dark .terminal:hover {
  box-shadow: 
    0 32px 64px -12px rgba(0, 0, 0, 0.6),
    0 0 0 1px rgba(255, 255, 255, 0.08) inset;
}

.terminal::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg,
    rgba(59, 130, 246, 0.03) 0%,
    rgba(147, 51, 234, 0.03) 100%);
  pointer-events: none;
  z-index: 1;
}

.dark .terminal::before {
  background: linear-gradient(135deg,
    rgba(59, 130, 246, 0.08) 0%,
    rgba(147, 51, 234, 0.08) 100%);
}

.terminal:hover::before {
  background: linear-gradient(135deg,
    rgba(59, 130, 246, 0.05) 0%,
    rgba(147, 51, 234, 0.05) 100%);
}

.dark .terminal:hover::before {
  background: linear-gradient(135deg,
    rgba(59, 130, 246, 0.12) 0%,
    rgba(147, 51, 234, 0.12) 100%);
}

.terminal-header {
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  background: rgba(241, 245, 249, 0.8);
  border-bottom: 1px solid rgba(203, 213, 225, 0.3);
  backdrop-filter: blur(8px);
}

.dark .terminal-header {
  background: rgba(30, 41, 59, 0.8);
  border-bottom: 1px solid rgba(71, 85, 105, 0.3);
}

.terminal-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 8px;
  transition: all 0.3s ease;
  cursor: pointer;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
}

.dark .terminal-dot {
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.3);
}

.terminal-dot:hover {
  transform: scale(1.2);
  box-shadow: 0 0 8px currentColor, inset 0 1px 2px rgba(0, 0, 0, 0.2);
}

.terminal-dot.red { 
  background: linear-gradient(135deg, #ff5f56, #e94b3c);
}
.terminal-dot.yellow { 
  background: linear-gradient(135deg, #ffbd2e, #f5a623);
}
.terminal-dot.green { 
  background: linear-gradient(135deg, #27ca3f, #22a034);
}

/* Terminal content with sharp text */
.terminal-content {
  padding: 1rem 1.5rem;
  font-family: 'SF Mono', Monaco, Inconsolata, 'Roboto Mono', Consolas, 'Courier New', monospace;
  font-size: 0.875rem;
  line-height: 1.6;
  color: #374151;
  background: transparent;
  position: relative;
  z-index: 10;
  min-height: 120px;
  overflow-x: auto;
  white-space: nowrap;
}

.dark .terminal-content {
  color: #f1f5f9;
}

/* Terminal syntax highlighting classes */
.terminal-prompt {
  color: #10b981;
}

.terminal-user {
  color: #3b82f6;
}

.terminal-path {
  color: #8b5cf6;
}

.terminal-command {
  color: #374151;
}

.dark .terminal-command {
  color: #e2e8f0;
}

.terminal-output {
  color: #6b7280;
  opacity: 0.9;
}

.dark .terminal-output {
  color: #94a3b8;
}

.terminal-string {
  color: #10b981;
}

.terminal-comment {
  color: #9ca3af;
  font-style: italic;
}

.terminal-cursor {
  color: #3b82f6;
}

.terminal-highlight {
  background-color: rgba(59, 130, 246, 0.1);
}

/* Terminal line prefixes */
.terminal-line {
  margin-bottom: 0.5rem;
  display: block;
}

.terminal-line:last-child {
  margin-bottom: 0;
}

/* Enhanced readability */
.terminal-content {
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  letter-spacing: 0.025em;
}

.dark .terminal-content {
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
  letter-spacing: 0.025em;
}

/* Terminal header title styling */
.terminal-title {
  color: var(--text-secondary);
  font-size: 0.875rem;
  font-weight: 500;
  opacity: 0.8;
  font-family: 'JetBrains Mono', monospace;
}

/* Selection in terminal */
.terminal-content ::selection {
  background-color: rgba(59, 130, 246, 0.2);
  color: inherit;
}

.dark .terminal-content ::selection {
  background-color: rgba(59, 130, 246, 0.3);
  color: inherit;
}

/* Animations with anti-blur */
@keyframes gradient-shift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

@keyframes float {
  0%, 100% { transform: translateY(0px) translateZ(0); }
  50% { transform: translateY(-10px) translateZ(0); }
}

@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 
      var(--paper-shadow-soft),
      0 0 0 rgba(37, 99, 235, 0);
  }
  50% {
    box-shadow: 
      var(--paper-shadow-medium),
      0 0 20px rgba(37, 99, 235, 0.3);
  }
}

@keyframes slide-in-up {
  from {
    opacity: 0;
    transform: translateY(30px) translateZ(0);
  }
  to {
    opacity: 1;
    transform: translateY(0) translateZ(0);
  }
}

@keyframes fade-in-scale {
  from {
    opacity: 0;
    transform: scale(0.8) translateZ(0);
  }
  to {
    opacity: 1;
    transform: scale(1) translateZ(0);
  }
}

@keyframes paper-fold {
  0% { transform: rotateY(-90deg); opacity: 0; }
  50% { transform: rotateY(-45deg); opacity: 0.5; }
  100% { transform: rotateY(0deg); opacity: 1; }
}

@keyframes typewriter {
  from { width: 0; }
  to { width: 100%; }
}

@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0; }
}

/* Enhanced paper ripple effect */
@keyframes paper-ripple {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(4);
    opacity: 0;
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .btn {
    padding: 0.875rem 1.25rem;
    font-size: 0.8rem;
  }
  
  .paper-card {
    border-radius: 12px;
  }
  
  .terminal {
    border-radius: 12px;
  }
  
  .terminal-content {
    padding: 1rem;
    font-size: 12px;
  }
}

/* Interactive hover effects with anti-blur */
.interactive-hover {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  transform: translateZ(0);
  will-change: transform;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.interactive-hover:hover {
  transform: translateY(-2px) scale(1.02) translateZ(0);
}

/* Paper Texture Utility */
.paper-texture {
  position: relative;
}

.paper-texture::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 25% 25%, rgba(139, 115, 85, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(139, 115, 85, 0.03) 0%, transparent 50%),
    linear-gradient(135deg, transparent 49%, rgba(139, 115, 85, 0.01) 50%, transparent 51%);
  background-size: 200px 200px, 150px 150px, 30px 30px;
  pointer-events: none;
  border-radius: inherit;
}

/* Status Indicators */
.status-online {
  background: var(--tech-green);
  box-shadow: 0 0 0 2px rgba(16, 185, 129, 0.2);
}

.status-typing {
  background: var(--tech-orange);
  animation: pulse-glow 2s infinite;
}

/* Badges */
.badge {
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid;
}

.badge-primary {
  background: var(--accent-blue);
  color: white;
  border-color: var(--accent-blue);
}

.badge-success {
  background: var(--tech-green);
  color: white;
  border-color: var(--tech-green);
}

.badge-paper {
  background: var(--bg-tertiary);
  color: var(--text-secondary);
  border-color: var(--border-primary);
  box-shadow: var(--paper-shadow-soft);
}

/* Motion Presets */
.motion-preset-fade-in {
  animation: fade-in-scale 0.6s ease-out;
}

.motion-preset-slide-up {
  animation: slide-in-up 0.6s ease-out;
}

.motion-preset-paper-fold {
  animation: paper-fold 0.8s ease-out;
  transform-origin: left center;
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* Focus Ring */
.focus-ring:focus-visible {
  outline: 2px solid var(--accent-blue);
  outline-offset: 2px;
  border-radius: 8px;
}

/* Scroll Animations */
.scroll-animate {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.6s ease-out;
}

.scroll-animate.in-view {
  opacity: 1;
  transform: translateY(0);
}

/* Loading States */
.skeleton {
  background: linear-gradient(
    90deg,
    var(--bg-tertiary) 25%,
    var(--bg-quaternary) 50%,
    var(--bg-tertiary) 75%
  );
  background-size: 200% 100%;
  animation: skeleton-loading 1.5s infinite;
  border-radius: 8px;
}

@keyframes skeleton-loading {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

/* Code Elements */
.code-block {
  background: var(--bg-tertiary);
  border: 1px solid var(--border-primary);
  border-radius: 8px;
  padding: 1rem;
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.875rem;
  line-height: 1.6;
  overflow-x: auto;
  box-shadow: var(--paper-shadow-soft);
}

/* Tech Accent Elements */
.tech-accent {
  position: relative;
}

.tech-accent::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(to bottom, var(--tech-green), var(--tech-blue));
  border-radius: 1px;
}

/* Enhanced Paper Fold Effect */
.paper-fold {
  position: relative;
  transform-style: preserve-3d;
}

.paper-fold::after {
  content: '';
  position: absolute;
  top: 0;
  right: -1px;
  width: 0;
  height: 100%;
  border-top: 20px solid transparent;
  border-bottom: 20px solid transparent;
  border-right: 20px solid var(--bg-primary);
  filter: drop-shadow(-2px 2px 4px rgba(139, 115, 85, 0.1));
}

/* Theme transition */
.theme-transition {
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Paper crease effects */
.paper-crease {
  position: relative;
}

.paper-crease::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(139, 115, 85, 0.1) 20%,
    rgba(139, 115, 85, 0.05) 50%,
    rgba(139, 115, 85, 0.1) 80%,
    transparent 100%
  );
  transform: translateY(-0.5px);
}

